import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListBranchHomeByMerchantIdHandle } from 'src/redux/actions/branch.action';
import { setCurrentLocation } from 'src/redux/actions/common.action';
import { getUserInfoSelector } from 'src/redux/selectors/auth';

import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import { sendFCMTokenToReceiveNotifications } from 'src/services/common.service';
import { askForPushNotificationPermission } from 'src/utils/firebaseCloudMessaging';
import HomeView from './about.view';

const HomeContainer = () => {
  const dispatch = useDispatch();
  const [listBranch1, setListBranch1] = useState([]);
  const [listSelectedBranch, setListSelectedBranch] = useState([]);
  const [openBranchModal, setOpenBranchModal] = useState(false);
  const currentUserInfo = useSelector(getUserInfoSelector);

  const [listBranch2, setListBranch2] = useState([]);
  useEffect(() => {
    dispatch(
      getListBranchHomeByMerchantIdHandle(934, 1, 100, (branches) => {
        setListBranch1(branches);
      })
    );
    dispatch(
      getListBranchHomeByMerchantIdHandle(935, 1, 100, (branches) => {
        setListBranch2(branches);
      })
    );
  }, []);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position) => {
      dispatch(setCurrentLocation(position.coords));
    });
  }, []);

  useEffect(() => {
    const LOCAL_FCM_TOKEN = localStorage.getItem('FCM_TOKEN');
    if (currentUserInfo?.id) {
      askForPushNotificationPermission(
        (fcmTokenAfterGrantedPushNotifications) => {
          localStorage.setItem(
            'FCM_TOKEN',
            fcmTokenAfterGrantedPushNotifications
          );
          if (LOCAL_FCM_TOKEN !== fcmTokenAfterGrantedPushNotifications) {
            sendFCMTokenToReceiveNotifications({
              user_id: currentUserInfo.id,
              token: fcmTokenAfterGrantedPushNotifications,
              os: 1,
            });
          }
        }
      );
    }
  }, [currentUserInfo]);

  const listBanner = useSelector(getListBannerSelector);
  return (
    <HomeView
      listBranch2={listBranch2}
      listBranch1={listBranch1}
      listBanner={listBanner}
      onBranchClick={(listBranch) => {
        setOpenBranchModal(true);
        setListSelectedBranch(listBranch);
      }}
      openBranchModal={openBranchModal}
      listSelectedBranch={listSelectedBranch}
      onCloseClick={() => {
        setOpenBranchModal(false);
      }}
    />
  );
};

export default HomeContainer;
